.testswitch {
  position: relative;
  float: left;
  width: 60px;
  margin: 0;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}
.testswitch-checkbox {
  display: none;
}
.testswitch-label {
  display: block;
  overflow: hidden;
  cursor: pointer;
  border-radius: 20px;
}
.testswitch-inner {
  display: block;
  width: 200%;
  margin-left: -100%;
  -webkit-transition: margin 0.1s ease-in 0s;
  transition: margin 0.1s ease-in 0s;
}
.testswitch-inner::before,
.testswitch-inner::after {
  display: block;
  float: right;
  width: 50%;
  height: 15px;
  padding: 0;
  line-height: 16px;
  font-size: 5px;
  color: white;
  font-family: Trebuchet, Arial, sans-serif;
  font-weight: bold;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.testswitch-inner::after {
  content: attr(data-on);
  padding-left: 10px;
  background-color: #0aa1ed;
  color: #FFFFFF;
}
.testswitch-inner::before {
  content: attr(data-off);
  padding-right: 5px;
  background-color: #EEEEEE;
  color: #999999;
  text-align: right;
}
.testswitch-switch {
  position: absolute;
  display: block;
  width: 10px;
  height: 10px;
  margin: 2px;
  background: #FFFFFF;
  top: 0;
  bottom: 0;
  right: 40px;
  border: 1px solid #999999;
  border-radius: 20px;
  -webkit-transition: all 0.1s ease-in 0s;
  transition: all 0.1s ease-in 0s;
}
.testswitch-checkbox:checked + .testswitch-label .testswitch-inner {
  margin-left: 0;
}
.testswitch-checkbox:checked + .testswitch-label .testswitch-switch {
  right: 0px;
}

